import React, { Component } from 'react';
import Header from './Header';
import TodoTaker from './TodoTaker';
import TodoList from './TodoList';

class TodoApp extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            title : "My Todo app",
            tasks : []
        }
        this.handleAddTodo = this.handleAddTodo.bind(this);
        this.removeTodo = this.removeTodo.bind(this);
    }
    handleAddTodo(title,notes,date){
        // this.setState({this.state.tasks})
        console.log("Title: ",title);
        console.log("Notes: ",notes);
        const newState = [{
            id :Math.random()*734837,
            title: title,
            notes: notes,
            date : date
            // value:task
        }];
        this.setState((currState) => ({
            tasks : currState.tasks.concat(newState)           
        }));
    }
    removeTodo(id){
        // this.state.tasks.splice(id);
        console.log("Id:",id);
        let array = this.state.tasks;
        array.forEach(myFunction);
        function myFunction(item,index){
            if(item.id == id)
                delete array[index];
        }
        this.setState({
            tasks : array
        })
    }
    render(){
        // console.log(this.state.tasks);
        return (<div><Header title={this.state.title}/>
            <TodoTaker handleAddTodo = {this.handleAddTodo}/>
            <TodoList tasks = {this.state.tasks} removeTodo={this.removeTodo}/>
            </div>);
    }
}
export default TodoApp;
